.container-game {
  -ms-flex-align: center;
      align-items: center; 
  background: -webkit-gradient(linear, left top, left bottom, from(#11998e), to(#38ef7d)); 
  background: -webkit-linear-gradient(top, #11998e, #38ef7d); 
  background: -o-linear-gradient(top, #11998e, #38ef7d); 
  background: linear-gradient(to bottom, #11998e, #38ef7d); 
  color: white;
  display: -ms-flexbox;
  display: flex;
  font-size: 30px;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  overflow: hidden;
  width: 100vw;
}

.game-title {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  height: 90vh;  
  max-width: 500px;
  padding: 0px 30px;
}


